export const event = "e1;Target;test;test;Error - Server side|||e2;Target;test;test;Error - Server client|||e3;Target;test;test;Success|||" +
"e4;Test;test;test;Error - Server side|||e5;Test;test;test;Success|||e6;Test;test;test;Success";

export async function handleFile(e) {
  let reader = new FileReader();
  let file = e.target.files[0]
  let format = (/[.]/.exec(file.name)) ? /[^.]+$/.exec(file.name)[0] : undefined;
  if (format !== 'txt') return {name: '', sheets: []}
  const readFile = () => {
    return new Promise((resolve, reject) => {
      reader.onload = async function(e) {
        try {
          const response = e.target.result;
          resolve(response);
        } catch (err) {
          reject(err);
        }
      };
      reader.onerror = (err) => {
        reject(err);
      };
      reader.readAsText(file, 'ISO-8859-1')
    });
  };
  let result = await readFile(e);
  let worksheets = [{sheetname: file.name.slice(0, -4), sheet: result}];
  return {format: format, name: file.name, sheets: worksheets};
};

export function filesTextToJSON(text) {
  let result = [];
  let rows = text.split('|||').filter(x => x !== '');
  let headers = ['EventID', 'File type', 'File name', 'Upload date', 'Upload status'];
  rows = rows.map((x) => (x.split(';')));
  rows.map((row) => {result.push(row.reduce((a, v, index) => ({ ...a, [headers[index]]: v}), {}))})
  return {headers: headers, values: result};
};

export function navig(path) {
  window.location.href = path
};

export async function checkContent(name, content) {

  let log = {show: true, error: [], mess: ''};
  let status = log.error.length > 0 ? 'error' : 'success';

  console.log(status);
  console.log(name);
  console.log(content.sheet);

  let response = await fetch('FilesUpload', {
    method: "POST",
    body: JSON.stringify({
      status: status,
      name: name,
      data: content.sheet
    })
  });
  if (response.ok) {
    let result = await response.text();
    if (status === 'success') {
      let rows = result.split('|||').filter(x => x !== '');
      let mess = rows.length ? 'Upload failed' : 'File content uploaded succesfully'
      return {log: {show: true, error: rows, mess: mess}}
    } else {
      return {log: log}
    };
  } else {
    alert("HTTP-Error: " + response.status);
    return {log: log, data: ''}
  }
};

export async function loadContent(filetype, event) {
  let response = await fetch('FilesHome', {
    method: "POST",
    body: JSON.stringify({
      event: event
    })
  });
  if (response.ok) {
    let result = await response.text();
    let csvContent = "data:text/csv;charset=utf-8,"+result;
    let encodeUri = encodeURI(csvContent);
    window.open(encodeUri);
  } else {
    alert("HTTP-Error: " + response.status);
  }
};