import * as React from 'react';
import { useState } from 'react';
import { HashRouter as Router, Routes, Route } from 'react-router-dom';
import Home from './Home';
import Upload from './Upload';
import SideNav from './SideNav';
import Log from './Log';
import HomeIcon from '@mui/icons-material/Home';
import UploadIcon from '@mui/icons-material/Upload';

const sections = [
  { icon: <HomeIcon />, text:  'Home', path: 'FilesHome' },
  { icon: <UploadIcon />, text: 'File upload', path: 'FilesUpload' }
]

export default function App( props ) {
  const [log, setLog] = useState({show: false, error: [], mess: ''});
  const [filesData, setFilesData] = useState(props.files);
  const [fileType, setFileType] = useState(props.filetype);
  const [workbook, setWorkbook] = useState({ format: null, name: null, sheets: [] });
	const [sheet, setSheet] = useState({ sheetname: null, header: [], data: [] });
	const [selectedSheet, setSelectedSheet] = useState(0);
  const [selectedHeader, setSelectedHeader] = useState(0);

	return (
		<div>
			<Router basename='/'>
				<SideNav appTitle='Files uploader' sections={ sections }/>
				<Routes>
          <Route 
            path='/FilesHome' 
            element={<Home 
              filesData={filesData}
            />} 
          />
          <Route 
            path='/FilesUpload' 
            element={<Upload 
              log={log}
              setLog={setLog}
              fileType={fileType} 
              setFileType={setFileType} 
              workbook={workbook} 
              setWorkbook={setWorkbook} 
              sheet={sheet} 
              setSheet={setSheet} 
              selectedSheet={selectedSheet} 
              setSelectedSheet={setSelectedSheet}
              selectedHeader={selectedHeader} 
              setSelectedHeader={setSelectedHeader}
            />} 
          />
          <Route 
            path='/FilesError' 
            element={<Log 
              log={log}
            />} 
          />
				</Routes>
			</Router>
		</div>
	);
}
