import * as React from 'react';
import Page from './Page.js';
import TableViewer from './TableViewer.js';

export default function Upload({ log, setLog, workbook, setWorkbook, sheet, setSheet }) {

  return (
    <Page name='File upload'>
      <TableViewer 
        log={log}
        setLog={setLog}
        workbook={workbook} 
        setWorkbook={setWorkbook} 
        sheet={sheet} 
        setSheet={setSheet} 
      />
    </Page>
  );
}
