import * as React from 'react';
import { handleFile, checkContent } from '../utils';
import { useState, useEffect } from 'react';
/* import TypeSelect from './TypeSelect.js' */
/* import SheetsSelect from './SheetsSelect.js' */
/* import HeaderSelect from './HeaderSelect.js' */
/* import CustomDataGrid from './CustomDataGrid'; */
import Log  from './Log';
/* import SettingsPopup from './SettingsPopup'; */
import Container from '@mui/material/Container';
import CircularProgress  from '@mui/material/CircularProgress';
import Button from '@mui/material/Button';
import InputLabel from '@mui/material/InputLabel';
import Typography from '@mui/material/Typography';

export default function TableViewer({ log, setLog, workbook, setWorkbook, sheet, setSheet }) {
  const [isLoading, setIsLoading] = useState(false);
  
  async function upload() {
    let result = await checkContent(workbook.name, sheet)
    setLog(result.log)
  };

  async function loadFile(e) {
/*     setIsLoading(true); */
/*     setSelectedSheet(0);
    setSelectedHeader(0); */
    setLog({show: false, error: [], mess: ''});
    let result = await handleFile(e);
    setWorkbook(result);
  };

  useEffect(() => {
    setSheet(workbook.sheets[0]);
    setLog({show: false, error: [], mess: ''});
/*     setIsLoading(false); */
  }, [workbook])

  return (
    <Container sx={{ maxWidth: '1500px !important', padding: '0px !important' }}>
      <Container sx={{ justifyContent: 'center', display: 'flex', width: '600px' }}>
{/*         <Typography sx={{ paddingRight: '10px', fontSize: '1.3rem', fontWeight: 'bold', color: '#1976d2' }}>Content type: </Typography> */}
      </Container>
      <br/>
        <Container sx={{ justifyContent: 'center', display: 'inline' }}>
          <Button variant="contained" component="label">
            Upload
            <input 
              multiple 
              hidden 
              accept=".txt" 
              type="file"
              id="input" 
              onChange={(e) => loadFile(e)}
              disabled={isLoading}
            />
          </Button>
          { workbook.name ? (<InputLabel sx={{ paddingLeft: '1rem', display: 'inline', verticalAlign: 'middle' }}> { workbook.name } </InputLabel>) : null }
        </Container>
      <br/>
{/*       <CircularProgress disableShrink={true} size={100} thickness={6}
        sx={{
          top: '350px', left: 'calc(50% - 50px)', position: 'absolute', color:"#bbdefb",
          ...(!isLoading && {
            visibility: 'hidden',
          })
        }}
      /> */}
      <br/>
      <Container sx={{ maxWidth: '1500px !important', ...(isLoading && {visibility: 'hidden'}) }}>
        { sheet !== undefined ? (
            <Container sx={{ maxWidth: '1500px !important', width: '100%', padding: '0px !important' }}>
              <Container sx={{ maxWidth: '1500px !important', width: '100%', padding: '0px !important', display: 'inline-flex' }}>

              </Container>
              <Button 
                variant="contained" 
                component="label"
                onClick={upload}
                sx={{
                  fontSize: '1rem',
                  fontWeight: 'bold',
                  backgroundColor: '#2e7d32',
                  color: '#dcedc8',
                  margin: '30px',
                  '&:hover': { backgroundColor: '#1b5e20' },
                }}
              >
                Submit
              </Button>
            </Container>
          ) : (<Typography sx={{ fontSize: '2.5rem', fontWeight: 'bold', paddingTop: '150px', color: '#ddd'}}>No Data</Typography>)
        }
        <Log 
          log={log}
        />
      </Container>
      <br/>
    </Container>
  );
}
