import * as React from 'react';
import { loadContent } from '../utils';
import Table from '@mui/material/Table';
import { styled } from '@mui/material/styles';
import TableBody from '@mui/material/TableBody';
import TableCell, { tableCellClasses } from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';

const StyledTableCellFirst = styled(TableCell)(({ theme }) => ({
  [`&.${tableCellClasses.head}`]: {
    position: 'sticky',
    left: 0,
    zIndex: 11,
    backgroundColor: theme.palette.primary.main,
    color: 'white',
    fontWeight: 'bold',
    padding: 5,
    minWidth: 60
  },
  [`&.${tableCellClasses.body}`]: {
    position: 'sticky',
    left: 0,
    zIndex: 10,
    fontSize: 14,
    padding: 5
  },
}));

const StyledTableCell = styled(TableCell)(({ theme }) => ({
  [`&.${tableCellClasses.head}`]: {
    backgroundColor: theme.palette.primary.main,
    color: 'white',
    fontWeight: 'bold',
    padding: 5,
    minWidth: 200
  },
  [`&.${tableCellClasses.body}`]: {
    fontSize: 14,
    padding: 5
  },
}));

const StyledTableRow = styled(TableRow)(({ theme }) => ({
  '&:nth-of-type(odd)': {
    backgroundColor: theme.palette.action.hover,
  },
  '&:last-child td, &:last-child th': {
    border: 0,
  },
}));

export default function FilesTable({ type, filesData }) { 
  const filteredFiles = filesData.values.filter((f) => (f["File type"] === type))
  return (
    <TableContainer sx={{ border: "1px solid rgba(224, 224, 224, 1)", maxHeight: '600px', width: '100%', height: 'max-content' }} component={Paper}>
      <Table stickyHeader aria-label="simple table">
        <TableHead>
          <TableRow>
          <StyledTableCellFirst key={0}>{filesData.headers[1]}</StyledTableCellFirst>
          {filesData.headers.slice(2).map((h, index) => (
            <StyledTableCell key={index}>{h}</StyledTableCell>
          ))}
          </TableRow>
        </TableHead>
        <TableBody>
          {filteredFiles.map((f, index) => (
            <StyledTableRow
              key={f["EventID"]}
              sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
            >
              <StyledTableCellFirst key={0}>{f["File type"]}</StyledTableCellFirst>
              { f["Upload status"] === 'Success' ? (
                  <StyledTableCell key={1} sx={{ cursor: 'pointer', color: 'blue' }} onClick={() => loadContent(f["File type"], f["EventID"])}>{f["File name"]}</StyledTableCell>
                ) : (
                  <StyledTableCell key={1}>{f["File name"]}</StyledTableCell>
                )
              }
              <StyledTableCell key={2}>{f["Upload date"]}</StyledTableCell>
              { f["Upload status"] === 'Success' ? (
                  <StyledTableCell key={3} sx={{ fontWeight: 'bold', color: '#00c853' }}>{f["Upload status"]}</StyledTableCell>
                ) : (
                  <StyledTableCell key={3} sx={{ color: '#000' }}>{f["Upload status"]}</StyledTableCell>
                )
              }
            </StyledTableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}