import React from 'react';
import ReactDOM from 'react-dom/client';
import { event, filesTextToJSON } from './utils';
import './index.css';
import App from './components/App';

const root = ReactDOM.createRoot(document.getElementById('root'));

const filetype = localStorage.getItem('filetype') !== null ? localStorage.getItem('filetype') : '';
const files = localStorage.getItem('files') !== null ? filesTextToJSON(localStorage.getItem('files')) : filesTextToJSON(event);

root.render(
  <React.StrictMode>
    <App filetype={filetype} files={files}/>
  </React.StrictMode>
);