import React from "react";
import { useEffect } from 'react';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';

export default function Log({ log }) {

  useEffect(() => {
    if (log.show) window.scrollTo({top: 750, behavior: 'smooth'})
  }, [log])

  return ( 
    log.show && (
      <Container sx={{ textAlign: 'center', padding: '0px !important' }}>
        <Typography sx={{ padding: '5px', fontSize: '1.2rem', fontWeight:'Bold' }}>{log.mess}</Typography>
        <Container sx={{ height: 'max-content', width: '100%', background: '#ffecb3', textAlign: 'left', borderRadius: '8px' }}>
          {log.error.map((x, index) =>
            <Typography key={index} dangerouslySetInnerHTML={{__html: '&nbsp;&nbsp;' + x}}></Typography>
          )}
        </Container>
        <br/>
      </Container>
    )
  );
}