import * as React from 'react';
import { navig } from '../utils'
import FilesTable from './FilesTable.js';
import Container from '@mui/material/Container';
import Typography  from '@mui/material/Typography';
import Button from '@mui/material/Button';
import Box from '@mui/material/Box';

export default function FilesPage({ filesData }) {

	const fileTypes = filesData.values.reduce((a, x) => a.includes(x['File type']) ? a : a.concat(x['File type']), []);

  return (
    <Container>
      {fileTypes.map((type, index) => (
        <Container key={index} sx={{ textAlign: 'left' }}>
          <Typography sx={{ fontSize: '1.5rem' }}>{ type }</Typography>
          <FilesTable
            type={type}
            filesData={filesData}
          />
          <Box sx={{ m: 1 }} />
          <Container sx={{ padding: '0px !important', justifyContent: 'right', display: 'inline-flex' }}>
            <Button 
              variant="contained" 
              component="label" 
              onClick={() => (navig('FilesUpload?filetype=' + type))}
              sx={{
                  backgroundColor: 'white',
                  color: '#000'              ,
                '&:hover': {
                  color: 'white',
                }
              }}
            >
              Add new
            </Button>
          </Container>
          <br/>
          <br/>
        </Container>
      ))}
    </Container>
  );
}